import React, { Component } from 'react'
import SnackbarError from '../components/SnackbarError'; 
import { Panel, PanelHeader, FormLayout, Header, Input, Select, Checkbox, Button, Div, RangeSlider, SelectMimicry, ScreenSpinner, SimpleCell, Cell, Switch } from '@vkontakte/vkui'
import Icon28AddCircleOutline from '@vkontakte/icons/dist/28/add_circle_outline';
import Icon28CancelCircleOutline from '@vkontakte/icons/dist/28/cancel_circle_outline';

const SEASONS_TRANSCRIPTION = {
  'all': 'любое',
  'winter': 'зима',
  'spring': 'весна',
  'summer': 'лето',
  'fall': 'осень',
};

const MONTHS_TRANSCRIPTION = {
  0: 'любой',
  1: 'январь', 2: 'февраль', 3: 'март', 4: 'апрель',
  5: 'май', 6: 'июнь', 7: 'июль', 8: 'август',
  9: 'сентябрь', 10: 'октябрь', 11: 'ноябрь', 12: 'декабрь',
};

const CONTINENT_DST = {
  A:"Любая",
  B:'Европа',
  C:"Африка",
  D:'Океания',
  E:"Азия",
  F:"Южная Америка",
  D:"Северная Америка"
}

const LANGUAGES = {
  de: "немецкий",
  en: "английский",
  es: "испанский",
  fr: "французский",
  it: "итальянский",
  ru: "русский",
  th: "тайский",
  tr: "турецкий"
}

const CURRENCY = {
  AZN: "манат",
  BYN: "беларусский рубль",
  CNY: "юань",
  EUR: "евро",
  KGS: "сом",
  KZT: "тенге",
  RUB: "рубль",
  THB: "бат",
  UAH: "гривна",
  USD: "доллар",
  UZS: "сум"
}

const PASSENGERS = {
  url: "1 пассажир, экономкласс",
  url1Business: "1 пассажир, бизнес-класс",
  url2Passengers: "2 пассажира, экономкласс",
  urlForma: "открытая редактируемая форма поиска"
}

export class Settings extends Component {
  state = {
    updated: "60",
    limit: "0",
    datesCnt: [{
      year: (new Date()).getFullYear(),
      season: 'all',
      month: '0'
    }],
    language: 'ru',
    profitability:30,
    currency: 'RUB',
    currencyForUrl: 'RUB',
    passengers:'url',
    randomCost:false,
    costMin: 100,
    costMax: 500000,
    dayTempMin:-50,
    dayTempMax:50,
    interval:'60',
    nighTempMin:-50,
    nighTempMax:50,
    days:[true, true, true, true, true, true, true],
    allDays:true,
    snackbar:null
  }


  onChange = e => {
    this.setState({
      [e.currentTarget.name]: e.currentTarget.value
    })
  }

  onChangeFrom = e => {
    const { datesCnt } = this.state
    datesCnt[parseInt(e.currentTarget.id, 10)][e.currentTarget.name] = e.currentTarget.value
    this.setState({
      datesCnt
    })
  }

  addFrom = () => {
    const { datesCnt } = this.state
    datesCnt.push({
      yearSrc: (new Date()).getFullYear(),
      seasonSrc: SEASONS_TRANSCRIPTION['all'],
      monthSrc: MONTHS_TRANSCRIPTION['0']
    })
    this.setState({
      datesCnt
    })
  }

  removeFrom = () => {
    const { datesCnt } = this.state
    this.setState({
      datesCnt: datesCnt.splice(0, datesCnt.length-1)
    })
  }

  onChangeDays = (e) => {
    console.log(e.currentTarget.name);
    if(e.currentTarget.name == 'all') {
      this.setState({ allDays:!this.state.allDays});
    }else{
      //this.setState({ allDays:!this.state.allDays});
      let bool = !this.state.days[Number(e.currentTarget.name)];
      let days = this.state.days.splice([Number(e.currentTarget.name)], 1, bool);
      console.log(this.state.days);
      
      let t = true;
      this.state.days.forEach(e => {
        if(!e) t = false
      })
      this.setState({ days:this.state.days, allDays:t });
    }

  }


  //проверка дней все или не все

  sort_res = () => { //готовим данные к отправке
    this.props.setPopout(<ScreenSpinner />)
    const { user, citySrc, cityDst, countryDst, countrySrc } = this.props.state;
    const { costMax, costMin, currency,currencyForUrl,language, randomCost, dual,forward, datesCnt, profitability, dayTempMin, dayTempMax, nighTempMax, nighTempMin,passengers, updated, interval, limit } = this.state;
    let days;
    if(this.state.allDays) {
      days = [true, true, true, true, true, true, true];
    }else{
      days = this.state.days;
    }
    let output = {
      citySrc, 
      cityDst,
      countryDst,
      countrySrc,
      costMax, 
      costMin,
      currency,
      currencyForUrl, 
      language,
      randomCost,
      forward:(forward === 'on'),
      dual:(dual === 'on'),
      passengers,
      updated:Number(updated),
      limit,
      interval,
      optional:true,
      dates:datesCnt,
      showAddionalWeather:true,
      profitability:String(profitability),
      ['nightTemp.max']:nighTempMax,
      ['nightTemp.min']:nighTempMin,
      ['dayTemp.max']:dayTempMax,
      ['dayTemp.min']:dayTempMin,
      days
    }
    let obj = {
      id:null,
      interval:interval,
      limit:limit,
      updated:Number(updated),
      user_id:user.id,
      output:output
    }
    this.save_request(obj);
    console.log(obj);
  }

  save_request = (obj) => {  //сохранение подписки
    fetch("https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/save-user-api-request", {
      "headers": {
        "accept": "*/*",
        "accept-language": "ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7",
        "content-type": "application/json;charset=utf-8",
      },
      "referrer": "https://appvk.flights.ru/",
      "referrerPolicy": "no-referrer-when-downgrade",
      "body": JSON.stringify(obj),
      "method": "POST",
    })
    .then(response => response.json())
    .then(check => {
      if(check.response === 'ok'){
        this.props.setPopout(null);
        console.log(check);
      }
    }).catch(err => {
        this.setState({snackbar:<SnackbarError close={() => this.setState({snackbar:null})} />})
        this.props.setPopout(null);
    })
  }


  render() {
    const {
      id
    } = this.props
    const {
      datesCnt,
      currency,
      currencyForUrl,
      language,
      countrySrc,
      citySrc,
      countryDst,
      cityDst,
      continentDst,
      randomCost,
      costMin,
      costMax,
      dayTempMin,
      dayTempMax,
      nighTempMax,
      nighTempMin,
      days
    } = this.state
    return (
      <Panel id={id}>
        <PanelHeader>Настройки</PanelHeader>
        <FormLayout>
          <Header>Откуда</Header>
          <SelectMimicry
            top="Страна вылета"
            name="countrySrc"
            data-type="country"
            placeholder="Россия"
            onClick={() => {
              this.props.changeDefaultName("countrySrc")
              this.props.changeDefault("Страна вылета")
              this.props.changeDefaultText(countrySrc)
              this.props.go("chooseCountry");
            }}
          >{this.props.state.countrySrc}</SelectMimicry>
          <SelectMimicry
            top="Город вылета"
            placeholder="Москва"
            name="citySrc"
            disabled={this.props.state.countrySrc == null}
            onClick={() => {
              this.props.changeDefaultName("citySrc")
              this.props.changeDefault("Город вылета")
              this.props.changeDefaultText(citySrc)
              this.props.go("chooseCity");
            }}
          >{this.props.state.citySrc}</SelectMimicry>
          <Header>Куда</Header>

          <Select
            top="Часть света"
            name="continentDst"
            onChange={this.onChange}
            value={continentDst}
          >
            {
              Object.keys(CONTINENT_DST).map((continent) => 
                <option value={continent} key={continent}>
                  {CONTINENT_DST[continent]}
                </option>
              )
            }
          </Select>

          <SelectMimicry
            top="Страна"
            name="countryDst"
            placeholder="Италия"
            onClick={() => {
              this.props.changeDefaultName("countryDst")
              this.props.changeDefault("Страна прибытия")
              this.props.changeDefaultText(countryDst)
              this.props.go("chooseCountry");   
             }          
            }
          >{this.props.state.countryDst}</SelectMimicry>
          <SelectMimicry
            top="Город"
            name="cityDst"
            placeholder="Рим"
            disabled={this.props.state.countryDst == null}
            onClick={() => {
              this.props.changeDefaultName("cityDst")
              this.props.changeDefault("Город прибытия")
              this.props.changeDefaultText(cityDst)
              this.props.go("chooseCity");   
             }          
            }
          >{this.props.state.cityDst}</SelectMimicry>
          <Header>Интервал</Header>
          <Select
            top="Получать дешевые авиабилеты один раз в:"
            name="interval"
          >
            <option value="60">Час</option>
            <option value="240">Четыре часа</option>
            <option value="480">Восемь часов</option>
            <option value="1440">24 часа</option>
          </Select>
          <Select
            top="Получать информацию за крайние N, часов"
            name="updated"
            onChange={this.onChange}
            defaultValue="60"
          >
            <option value="60">1 час</option>
          </Select>
          <Header>Количество</Header>
          <Select
            top="Сколько дешевых авиабилетов показывать в сутки?"
            name="limit"
            defaultValue="0"
          >
            <option value="0">Все</option>
            <option value="20">20</option>
            <option value="50">50</option>
            <option value="100">100</option>
            <option value="300">300</option>
          </Select>
          <Header>Полеты</Header>
          <Checkbox name="forward" onChange={this.onChange}>Туда</Checkbox>
          <Checkbox name="dual" onChange={this.onChange}>Туда-обратно</Checkbox>
          <Header>Когда</Header>
          {
            datesCnt && (
              datesCnt.map((item, i) => 
                <FormLayout key={i}>
                  <Select
                    top={`Год вылета (${i+1})`}
                    name="year"
                    id={i}
                    onChange={this.onChangeFrom}
                    value={item.yearSrc}
                  >
                    {
                      Array.from(Array(31).keys()).map((year) => 
                        <option key={year} vaule={(new Date()).getFullYear()+year}>
                          {(new Date()).getFullYear()+year}
                        </option>
                      )
                    }
                  </Select>
                  <Select
                    top={`Время года (${i+1})`}
                    name="season"
                    id={i}
                    onChange={this.onChangeFrom}
                    value={item.seasonSrc}
                  >
                    {
                      Object.keys(SEASONS_TRANSCRIPTION).map((season) => 
                        <option value={season} key={season}>
                          {SEASONS_TRANSCRIPTION[season]}
                        </option>
                      )
                    }
                  </Select>
                  <Select
                    top={`Месяц года (${i+1})`}
                    name="month"
                    id={i}
                    onChange={this.onChangeFrom}
                    value={item.monthSrc}
                  >
                    {
                      Object.keys(MONTHS_TRANSCRIPTION).map((month) => 
                        <option value={month} key={month}>
                          {MONTHS_TRANSCRIPTION[month]}
                        </option>
                      )
                    }
                  </Select>
                </FormLayout>
              )
            )
          }
          {
            datesCnt.length > 1
          }
          <Div style={{ display: 'flex' }}>
            {
              datesCnt.length < 3 && (
                <Button
                  size="l"
                  onClick={this.addFrom}
                >
                  <Icon28AddCircleOutline />
                </Button>
              )
            }
            {
              datesCnt.length > 1 && (
                <Button
                  size="l"
                  mode="destructive"
                  onClick={this.removeFrom}
                >
                  <Icon28CancelCircleOutline color="rgb(255, 26, 0)" />
                </Button>
              )
            }
          </Div>
          <Select
            top="Язык"
            name="language"
            onChange={this.onChange}
            value={language}
          >
            {
              Object.keys(LANGUAGES).map((language) => 
                <option value={language} key={language}>
                  {LANGUAGES[language]}
                </option>
              )
            }
          </Select>
          <Select
            top="Валюта"
            name="currency"
            onChange={this.onChange}
            value={currency}
          >
            {
              Object.keys(CURRENCY).map((currency) => 
                <option value={currency} key={currency}>
                  {CURRENCY[currency]}
                </option>
              )
            }
          </Select>
          <Select
            top="Валюта метапоисковика"
            name="currencyForUrl"
            onChange={this.onChange}
            value={currencyForUrl}
          >
            {
              Object.keys(CURRENCY).map((currency) => 
                <option value={currency} key={currency}>
                  {CURRENCY[currency]}
                </option>
              )
            }
          </Select>
          <Select
            top="Пассажиры"
            name="passengers"
            onChange={this.onChange}
          >
            {
              Object.keys(PASSENGERS).map((passengers) => 
                <option value={passengers} key={passengers}>
                  {PASSENGERS[passengers]}
                </option>
              )
            }
          </Select>
          <Header>Стоимость, {currency}</Header>
          <Checkbox
            checked={randomCost}
            onChange={(e) => {
              this.setState({
                randomCost: e.currentTarget.checked
              })
            }}
          >
            Любая
          </Checkbox>
          <RangeSlider
            top={costMin + " - " + costMax}
            min={100}
            max={500000}
            step={100}
            defaultValue={[100, 500000]}
            value={[costMin, costMax]}
            onChange={(value) => {
              this.setState({
                costMin: value[0],
                costMax: value[1],
                randomCost: false
              })
            }}
          />
          <Div style={{ display: 'flex' }}>
            <Input
              onFocus={() => {
                console.log('focus')
              }}
              value={costMin}
              type="number"
              onChange={(e) => {
                const value = parseInt(e.currentTarget.value, 10)
                if(value){
                  if(value < costMax && value > 100){
                    this.setState({ costMin: value })
                  }
                }
              }}
            />
            <Input
              value={costMax}
              type="number"
              onChange={(e) => {
                const value = parseInt(e.currentTarget.value, 10)
                if(value){
                  if(value > costMin && value < 500000){
                    this.setState({ costMax: value })
                  }
                }
              }}
            />
          </Div>
          <Header>Дополнительные параметры</Header>
          <Select name='profitability' onChange={this.onChange} top="Показывать авиабилеты только с выгодой больше, %">
            <option>30</option>
            <option>40</option>
            <option>50</option>
            <option>60</option>
            <option>70</option>
            <option>80</option>
            <option>90</option>
          </Select>
          <Header>Вылеты в определенные дни недели</Header>
            <Cell indicator={<Checkbox checked={this.state.allDays} name='all' onChange={this.onChangeDays}/>}>Любой</Cell>
            <Div style={{marginTop:-20}}>
              <Cell indicator={<Checkbox style={{ padding:0}} name='0' onChange={this.onChangeDays} checked={days[0] || this.state.allDays} />}>Понедельник</Cell>
              <Cell indicator={<Checkbox style={{ padding:0}} name='1' onChange={this.onChangeDays} checked={days[1] || this.state.allDays}/>} >Вторник</Cell>
              <Cell indicator={<Checkbox style={{ padding:0}} name='2' onChange={this.onChangeDays} checked={days[2] || this.state.allDays}/>}>Среда</Cell>
              <Cell indicator={<Checkbox style={{ padding:0}} name='3' onChange={this.onChangeDays} checked={days[3] || this.state.allDays}/>}>Четверг</Cell>
              <Cell indicator={<Checkbox style={{ padding:0}} name='4' onChange={this.onChangeDays} checked={days[4] || this.state.allDays}/>}>Пятница</Cell>
              <Cell indicator={<Checkbox style={{ padding:0}} name='5' onChange={this.onChangeDays} checked={days[5] || this.state.allDays}/>}>Суббота</Cell>
              <Cell indicator={<Checkbox style={{ padding:0}} name='6' onChange={this.onChangeDays} checked={days[6] || this.state.allDays}/>}>Воскресенье</Cell>
            </Div>
          <Header>Погода</Header>
          <RangeSlider
            top="Температура воздуха днем в день прилета"
            bottom={"От " + dayTempMin + " до " + dayTempMax}
            min={-50}
            max={50}
            step={1}
            defaultValue={[-50, 50]}
            value={[dayTempMin, dayTempMax]}
            onChange={(value) => {
              this.setState({
                dayTempMin: value[0],
                dayTempMax: value[1],
                randomCost: false
              })
            }}
          />
          <RangeSlider
            top="Температура воздуха ночью в день прилета"
            bottom={"От" + nighTempMin + " до " + nighTempMax}
            min={-50}
            max={50}
            step={1}
            defaultValue={[-50, 50]}
            value={[nighTempMin, nighTempMax]}
            onChange={(value) => {
              this.setState({
                nighTempMin: value[0],
                nighTempMax: value[1],
                randomCost: false
              })
            }}
          />
          <Button mode='primary' size='xl' onClick={this.sort_res}>Применить</Button>
        </FormLayout>
        {this.state.snackbar}
      </Panel>
    )
  }
}

export default Settings 
